
.left-scroll{
    overflow-y: auto;
    bottom: 0;
    position: absolute;
    top: 92px;
    width: 170px;
}
.move{
    position: absolute;
    top: 35px;
    left: -13px;
    width: 25px;
    height: 20px;
    line-height: 20px;
    background: #222;
    color: #fff;
    padding: 0 5px;
    z-index: 1112;
    font-size: 12px;
    border-radius: 3px;
    opacity: 0.9;
    display: none;
}
.move:before{
    position: absolute;
    top: -5px;
    width: 0;
    height: 0;
    left: 50%;
    margin-left: -5px;
    content: " ";
    border-bottom: 5px solid  #222 ;
    border-left: 5px solid transparent ;
    border-right: 5px solid transparent ;
}
.big-manage-wrap li{
    position: relative;
}
.big-manage-wrap li:hover .move{
    display: block;
}
.lists{
  position: absolute;
  width: 150px;
  border-bottom: 1px solid #dce4eb;
  line-height: 40px;
  height: 40px;
  overflow: hidden;
  padding-left: 20px;
  z-index: 1;
}
.dummy{
  border: 0px solid rgb(220, 228, 235)!important;
}
.clickArea{
   height: calc(100vh - 170px);
   position: relative;
}



