
.posAb{
    position: absolute;
    z-index: 1;
}
.wf-help{
    display: inline-block;
    width: 20px;
    height: 20px;
    float: none;
    margin-right: 0;
    background: url(../../../../static/images/wenhao.png) no-repeat;
    vertical-align: middle;
    margin-top: -2px;
}
.wf-help div{
    position: absolute;
    width: 200px;
    left: 17px;
    background: #fff;
    color: #666;
    z-index: 1111;
    line-height: 20px;
    padding: 5px;
    border: 1px solid #ccc;
    word-break: break-all;
    display: none;
}
.wf-help:hover{
    cursor: pointer;
}
.wf-help:hover div{
    display: block;
}



