
.canvas-slider-text {
    float: right;
    line-height: 36px;
}
.big-bottom-content {
    position: fixed;
    bottom: 0;
    height: 36px;
    left: 172px;
    right: 310px;
    background-color: #fff;
    padding: 0 10px 0 20px;
    border: 1px solid #dce4eb;
}
.canvas-slider {
    width: 200px;
    float: right;
    padding-right: 20px;
}
.sub-sub {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.big-content {
    min-width: auto;
    position: fixed;
    top: 50px;
    bottom: 38px;
    left: 180px;
    right: 320px;
    padding: 0;
    overflow: auto;
}
.big-content .big-sub-content {
    position: relative;
    -webkit-transform-origin: left top;
            transform-origin: left top;
    overflow: visible;
}
.big-content .item {
    border: 1px solid transparent;
    position: absolute;
}
.big-content .item>span {
    visibility: hidden;
}
.big-content .item span:hover {
    cursor: pointer;
}
.big-content .item:hover span {
    visibility: visible;
    z-index: 10;
    position: relative;
}
.big-content .item:hover {
    border: 1px solid #2cabff;
    cursor: move;
}
.big-content .ui-icon {
    background: url(../../../images/big-icon.png) no-repeat -159px -89px;
    visibility: hidden;
}
.big-content .item:hover .ui-icon, .big-content .item.active .ui-icon {
    visibility: visible;
}
.big-sub-content {
    height: 100%;
    background-size: cover;
}
.layer-canvas {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
}
.item.active {
    border: 1px solid #2cabff;
}
.selectBorder {
    border: 1px solid #2cabff !important;
}
.item.checked {
    background-color: rgba(0,0,0,0.1);
}
.white {
    color: #fff;
    font-size: 64px;
}
.el-silder__button,
  .el-slider__bar,
  .el-slider__button.dragging,
  .el-slider__button.hover,
  .el-slider__button:hover {
    background-color: #2cabff;
}
.item .layer-icon i.icon-trash {
    background: url(../../../../static/images/trash.svg) no-repeat;
    background-size: 100% 100%;
}
.item .layer-icon i.icon-eye {
    background: url(../../../../static/images/eye.svg) no-repeat;
    background-size: 100% 100%;
}
.item .layer-icon i.icon-copy {
    background: url(../../../../static/images/copy.svg) no-repeat;
    background-size: 100% 100%;
}
video::-internal-media-controls-download-button {
    display: none;
}
video::-webkit-media-controls-enclosure {
    overflow: hidden;
}
video::-webkit-media-controls-fullscreen-button {
    display: none;
}
video::-webkit-media-controls {
    overflow: hidden !important
}
video::-webkit-media-controls-enclosure {
    width: calc(100% + 32px);
    margin-left: auto;
}



