
.warn-content {
  font-family: 'Microsoft Yahei';
  position: fixed;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: #fff;
  z-index: 1000;
}
.warn-content .helpful {
  text-align: center;
  margin: 224px 200px 30px;
  font-size: 20px;
  color: #4a4a4a;
}
.warn-content .helpful p {
  margin: 0 0 10px;
}
.warn-content .helpful .greenBtn {
  width: 100px;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
  margin-top: 25px;
}
.warn-content .helpful .browser-info {
  margin: 40px;
  font-size: 12px;
  color: #757272;
}
.warn-content .helpful code {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}
.warn-content .helpful .keep {
  margin-top: 10px;
}
.warn-content .helpful .keep a {
  color: #2aafff;
  text-decoration: underline;
}



