
.source-menu li,.myScreen-title .fold-txt,.blueLink{
    cursor: pointer;
}
.tipFixed{
    position: fixed;
    right: 20px;
}
.myScreen-left{
    overflow-y: auto;
    position: absolute;
    bottom: 0;
    top: 84px;
    width: 198px;
}
.myScreen-left h3{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: 120px
}
.source-menu.myScreen-left>li{
    position: relative;
}
.itxt-error {
    border: 1px solid #ff0000;
}
.tips.red {
    color: #ff0000;
}
.newdrop{
    z-index: 11;
    top: 26px;
}
.newdrop.open{
    display: block;
}
.newdrop li{
    padding: 0;
    line-height: 30px;
}
.newdrop li a{
    display: block;
    padding: 0 5px;
    line-height: 30px;
    text-align: left;
}
html{
    overflow-x: auto;
}



