
.big-subitem-list li {
  width: 40%;
}
.big-subitem-list li .inputMode {
  padding-right: 0;
  width: 75px;
}
.big-listItem {
  padding-bottom: 20px;
}
.big-listItem li {
  padding-top: 0;
}
.tt {
  text-align: center;
}
.cover,
.cover img {
  margin: 0 auto;
  width: 200px;
  height: 125px;
}
.cover {
  background-color: #333;
  margin-top: 20px;
}
.btn-box {
  text-align: center;
  margin: 20px 0px 0 0;
}
.big-rule-box .tit,
.big-rule-box .scales,
.big-rule-box .min-input {
  margin-left: 20px;
}
.big-rule-box .con {
  padding-left: 20px;
}
.reset-bg {
  display: block;
  text-align: center;
}
.bg-uploader {
  width: 200px;
  height: 100px;
  text-align: center;
  border: 1px dashed #C4C6CF;
}
.bg-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.bg-uploader .el-upload--text {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  text-align: center;
}
.bg-uploader .bg,
.bg-uploader .bg-uploader-icon {
  width: 45px;
  height: 45px;
  margin: 0 auto;
  margin-top: 17px;
  background-color: #333;
}
.bg-uploader .bg-upload__text {
  color: #999;
}



