
.big-left,
.big-content,
.big-bottom-content,
.big-right {
  transition: all .3s ease;
}
.big-left.switch-on {
  height: 50px;
  left: -140px;
  z-index: 1;
}
.big-right.switch-on {
  right: -280px;
  height: 50px;
  z-index: 1;
}
.big-content.switch-on {
  right: 0px;
}
.big-content.switch-on + .big-bottom-content {
  right: 0;
}
.big-left.switch-on + div .big-content {
  left: 0;
}
.big-left.switch-on + div .big-bottom-content {
  left: 0;
}
.big-left.switch-on .big-manage-wrap {
  visibility: hidden;
}
.big-left .mt span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.big-right .mt i.big-i3,
.big-right .mt i.big-i4 {
  margin: 0;
  margin-left: 10px;
  cursor: pointer;
}
.big-right .mt li {
  width: 120px !important;
}



