    margin-top: -3px;
}
#echartBmap .el-input-number--small {
    width: 60px;
}
#echartBmap .el-input-number--small .el-input__inner {
    padding-right: 0;
    width: 60px;
    border-radius: 0;
}
.big-nav span.el-checkbox__input ,
.big-nav span.el-checkbox__inner{
    display: inline-block;
    float: none;
}
