
.radio-box {
  margin: 0 10px 0 8px;
  cursor: default;
}
.slider-wrap {
  width: 150px;
  margin: 4px 0 0 7px;
}
.slider-wrap .el-slider__button-wrapper {
  top: -20px;
}
#m-setting {
  /*overflow: auto;
     overflow-x:hidden;*/
  min-height: 600px;
}
#m-setting > dl > dd {
  display: none;
}
#m-setting > dl > dd.open {
  display: block;
}
#m-setting .u-addseries {
  display: block;
  cursor: pointer;
}
.m-setting .piece {
  margin-right: 20px;
}
.u-linear {
  width: 10px;
  height: 69px;
  background: -webkit-linear-gradient(top, #ccc, #000);
}
.g-ml24 {
  margin-left: 24px;
}
.g-pad5 {
  padding-left: 5px;
}
.g-w60 {
  width: 60px;
}
.g-w90b {
  width: 90%;
}
.m-setting .g-w90b {
  width: 90%;
}
.m-setting .el-slider__button-wrapper {
  top: -20px;
  z-index: 901;
}
.u-numdes {
  line-height: 18px;
  text-align: center;
}
.u-deleteseries {
  margin-left: 10px;
}
.g-series {
  border-bottom: 1px solid #DCDEE3;
}
.u-select {
  border: 1px solid #e7e7e7;
  height: 30px;
  line-height: 30px;
  outline: 0;
  color: #333;
}
.big-nav .u-addcolor {
  display: block;
  float: none;
  margin-left: 30px;
}



