
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-size: cover;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar {
  display: none;
  width: 0;
  background-color: #fff;
}
body {
  padding: 0;
  height: 100%;
  -webkit-transform-origin: 'left top 0px';
          transform-origin: 'left top 0px';
}
.preview {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
.preview .big-sub-content {
  position: relative;
  -webkit-transform-origin: left top;
          transform-origin: left top;
  margin-top: 0;
  background-color: transparent;
}
.preview .item {
  position: absolute;
}
.preview .item:hover {
  border: 0;
}
.preview .big-sub-content {
  height: 100%;
}
.preview .layer-canvas {
  height: 100%;
  width: 100%;
}
.white {
  color: #fff;
  font-size: 64px;
}
.shade-model {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(222, 222, 222, 0.2);
}
.preview .inputPass .btn {
  display: inline-block;
  border-radius: 3px;
  text-align: center;
  background: #3e8acc;
  color: #fff;
  width: 70px;
  height: 30px;
  line-height: 30px;
  margin-left: 10px;
}
.preview .inputPass .inputMode {
  vertical-align: top;
}
.preview .inputPass {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 50%;
  padding: 10px;
  text-align: center;
  color: #fff;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.preview .inputPass p {
  color: #fff;
  text-align: center;
  padding-bottom: 10px;
  font-size: 16px;
}
video::-internal-media-controls-download-button {
  display: none;
}
video::-webkit-media-controls-enclosure {
  overflow: hidden;
}
video::-webkit-media-controls-fullscreen-button {
  display: none;
}
video::-webkit-media-controls {
  overflow: hidden !important;
}
video::-webkit-media-controls-enclosure {
  width: calc(107%);
  margin-left: auto;
}



